.Nav-bar{
  background-color: #0078D7;
  display: flex;
  /* justify-content: space-between; */
  align-items: center;
  width: 100%;
}
.app-name{
  width: 15%;
}
.app-name>span,.app-name>span>a{
  justify-content: center;
/* color: white; */

  width: 100%;
  height: 100%;
  /* font-size: 1.2rem; */
  font-weight: bold;
}
.app-name>span>a{
    /* margin-left: 1rem; */
  text-decoration: none;
  background: transparent;
  color: white;
  font-size: 1.2rem;
}




.search-bar{
  
  width: 75%;
  display: flex;
  align-items: center;

}
.search-bar>div{
  width: 100%;
  padding-left: 10px;
  padding-right: 36px;
}
.search-bar input{
  padding-left: 27px;
  /* padding-left: 27px; */
  outline: none;
  border: 0;
}
.Nav-bar-option{
  margin-left: 6px;
  margin-right: 8px;
  width: 10rem;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.Nav-bar-option i{
  color: white;
}
.Nav-bar-option img{
  width: 1.5rem;
  border: 51%;
  border-radius: 50%;
  height: 1.5rem;
}
@media screen and (max-width:500px) {
  .Nav-bar-option{

    width: 112px;

  }
  .app-name>span a{
    font-size: 1rem;
    margin-left: 3px;
  }
}

@media screen and (max-width:362px){
  .app-name>span a{
    font-size: 0.8rem;
    margin-left: 0px;
  }
  .app-name{
    margin-right: 5px;
    text-align: center;
    line-height: 1;
  }
}






