.toltip {
    position: relative;
    /* display: inline-block; */
      /* border-bottom: 1px dotted black; If you want dots under the hoverable text */
  }
  
  /* Tooltip text */
  .tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: #555;
    color: #fff;
    text-align: center;
    padding: 5px 0;
    border-radius: 6px;
  
    /* Position the tooltip text */
    position: absolute;
    z-index: 100;
    bottom: -100%;
    left: 50%;
    margin-left: -60px;
  
    /* Fade in tooltip */
    opacity: 0;
    transition: opacity 0.3s;
  }
  
  /* Tooltip arrow */

  
  /* Show the tooltip text when you mouse over the tooltip container */
  .toltip:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
  }
  .first-hover i:hover{
    color: chartreuse;
  }

  #important_menu,#deleted{
    position: absolute;
    left: -48px;
    top: 50px;
    padding: 13px;
    border-radius: 8px;
    background: bisque;
  }